.wrapper {
  max-width: 1200px;
  margin-left: 15px;
  margin-right: 15px;
  width: 100%; }

.header {
  height: 100vh;
  width: 100%;
  background-image: url("../images/cover.jpg");
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }
  .header__nav {
    display: inline-block;
    background-color: #0271bc;
    height: 44px;
    width: 44px;
    position: fixed;
    top: 0px; }
    .header__nav--image {
      padding: 15px; }
  .header__box {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #ffffff;
    height: 50%;
    margin-bottom: 50px; }
    .header__box--title {
      font-family: "Roboto", sans-serif;
      text-transform: uppercase;
      font-size: 42px;
      display: flex;
      flex-direction: column;
      align-items: center; }
      .header__box--title span {
        display: block; }
    .header__box--quote {
      margin-top: 10px;
      margin-bottom: 45px; }
    .header__box--arrow {
      height: 45px;
      width: 45px;
      background-color: #ffffff;
      border-radius: 50%;
      display: inline-block;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      margin-top: 45px; }

.looking {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  margin-top: 30px;
  margin-bottom: 45px; }
  .looking__small {
    color: #8ccbea;
    display: inline-block;
    padding-top: 5px;
    padding-bottom: 5px; }
  .looking__title {
    color: #000000;
    font-style: "Roboto", sans-serif;
    display: block;
    margin-bottom: 15px; }
  .looking__text {
    margin-bottom: 30px;
    display: block; }
  .looking__link {
    color: #f26002;
    border: solid #f26002 1px;
    border-radius: 50px;
    display: inline;
    margin-bottom: 45px; }
    .looking__link a {
      padding-left: 25px;
      padding-right: 24px;
      padding-top: 10px;
      padding-bottom: 10px; }

.reasons {
  background-color: #eff9ff;
  display: block;
  text-align: center; }
  .reasons__link {
    color: #ffffff;
    background-color: #f26002;
    border-radius: 50px;
    display: inline-block;
    margin-bottom: 45px; }
    .reasons__link a {
      padding-left: 25px;
      padding-right: 24px;
      padding-top: 10px;
      padding-bottom: 10px; }
  .reasons__arrow {
    height: 45px;
    width: 45px;
    background-color: #ffffff;
    border-radius: 50%;
    display: inline-block;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 33px;
    position: absolute;
    transform: translate(345%, -120%); }

.columns {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: #304268;
  color: #ffffff;
  text-transform: uppercase; }
